<template>
  <div>
    <b-card title="Add user" style="max-width: 28rem;" class="mb-2">
      <b-card-text>
        <b-form-group>
          <b-form-input v-model="form.username" placeholder="name"></b-form-input>
        </b-form-group>
        <b-form-group>
          <b-form-input type="password" v-model="form.password" placeholder="password"></b-form-input>
        </b-form-group>
      </b-card-text>
      <b-button variant="success" @click="postForm">Save</b-button>
      <b-button variant="warning" class="ml-2" @click="$store.commit('endAction')">Cancel</b-button>
    </b-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    postForm() {
      let vue = this;
      vue.$axios
        .post("/login", {
          username: this.form.username,
          password: this.form.password
        })
        .then(function(response) {
          vue.$cookies.set("quidkey", response.data.key);
          vue.$store.commit("authenticate", response.data.key);
        })
        .catch(e => {
          console.log(e);
          if (e.response.status === 401) {
            vue.$bvToast.toast("Login refused", {
              title: "Authentication failed",
              variant: "danger"
            });
          }
        });
    }
  }
};
</script>