<template>
  <div>
    <h1 class="text-muted mt-3">Users</h1>
    <div>
      <b-collapse id="collapse-4" v-model="showActionBar" class="mt-2">
        <add v-if="action === 'addUser'"></add>
      </b-collapse>
    </div>
    <b-table striped hover :items="data" :fields="fields" class="mt-4" style="max-width:450px"></b-table>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import Add from "@/components/users/Add";

export default {
  components: {
    Add
  },
  data() {
    return {
      data: [],
      fields: ["id", "name"]
    };
  },
  methods: {
    fetchUsers() {
      this.$axios
        .get("/users/all")
        .then(response => (this.data = response.data))
        .catch(error => console.log(error));
    }
  },
  mounted: function() {
    this.fetchUsers();
  },
  computed: {
    ...mapState(["action"]),
    ...mapGetters({
      s: "showActionBar"
    }),
    showActionBar: {
      get() {
        return this.s;
      },
      set(newName) {
        return newName;
      }
    }
  }
  //computed: mapState(["action", "showActionBar"])
};
</script>